<template>
  <section class="rightlist-grszzl">
    <h2>个人设置</h2>
    <div class="timesx">
      <a href="#" class="active">个人资料</a>
      <a href="#">账户安全</a>
    </div>
    <form action="">
      <!-- 个人资料 -->
      <ul class="personaldata" >
        <li>
          <div class="img"><img :src="set.head_img" alt=""></div>
          <!-- <div class="img"><img src="../assets/images/tx.png" alt=""></div> -->
          <router-link to="" class="revise">修改头像</router-link>
        </li>
        <li>
          <p for="" class="red">昵称</p>
          <input type="text" value="" placeholder="请输入昵称" ref="inputname">
        </li>
        <li class="tex">
          <p for="" class="red">签名</p>
          <textarea name="" id="" cols="30" rows="10" placeholder="请输入你的个性签名"></textarea>
        </li>
        <li>
          <p for="" class="red">性别</p>
          <input type="radio" name="sex" id="boy" checked="checked"><label for="boy" class="check" > 男</label>
          <input type="radio" name="sex" id="girl"><label for="girl" >女</label>
        </li>
        <li>
          <p for="">生日</p>
          <div class="time">
            <div class="date">
              <select name="" id="">
                <option value="">请选择</option>
                <option value="">1993</option>
                <option value="">1992</option>
                <option value="">1991</option>
              </select>
              <div class="icon"><img src="../assets/images/date.png" alt=""></div>
            </div>
            <span>年</span>
            <div class="date">
              <select name="" id="">
                <option value="">请选择</option>
                <option value="">8</option>
                <option value="">7</option>
                <option value="">6</option>
              </select>
              <div class="icon"><img src="../assets/images/date.png" alt=""></div>
            </div>
            <span>月</span>
            <div class="date">
              <select name="" id="">
                <option value="">请选择</option>
                <option value="">13</option>
                <option value="">12</option>
                <option value="">11</option>
              </select>
              <div class="icon"><img src="../assets/images/date.png" alt=""></div>
            </div>
            <span>日</span>
          </div>
        </li>
        <li>
          <p for="" class="red">领域</p>
          <input type="text" value="" placeholder="请输入领域">
        </li>
        <li>
          <p for="">邮箱</p>
          <input type="text" value="" placeholder="请输入邮箱地址">
        </li>
        <li class="tex">
          <p for="">自我介绍</p>
          <textarea name="" id="" cols="30" rows="10" placeholder="请输入你的自我介绍">{{set.myself}}</textarea>
        </li>
        <li class="tex">
          <p for="" style="height:128px;">所在地地址</p>
          <div class="address">
            <select name="" id="">
                <option value="">省/直辖市</option>
                <option value="">河北省</option>
                <option value="">河南省</option>
              </select>
              <select name="" id="">
                <option value="">区/县</option>
                <option value="">区/县</option>
                <option value="">区/县</option>
              </select>
              <select name="" id="">
                <option value="">乡镇/街道</option>
                <option value="">乡镇/街道</option>
                <option value="">乡镇/街道</option>
                <option value="">乡镇/街道</option>
              </select>
              <textarea name="" id="" cols="30" rows="10" placeholder="详细地址"></textarea>
          </div>
        </li>
        <li>
          <input type="submit" value="确定">
        </li>
      </ul>
    </form>
  </section>

</template>
<script>
export default {
	name: 'ucsetting',
	data(){
		return{
			set:[],
			inputname:"昵称",
		}
	},
	created(){
		this.getSetting();
	},
	methods:{
		// 获取个人设置
		getSetting(){
			this.$axios.post('/pcgc/getdatum', {
				PCTOKEN: this.$store.state.token ? this.$store.state.token : localStorage.getItem('token'),
			})
			.then((res) => {
				console.log(res),
				this.set=res.data.data;
				console.log(this.set)
				switch (res.data.code) {
					case '0':
						alert(res.data.msg)
						break;
					case '1':
						alert(res.data.msg)
						break;
					// case '2':
					// 	alert(res.data.msg)
					// 	break;
					default:
						alert(res.data.msg)
						break;
				}
				
			})
			.catch((err) => {})
		},
	}	
}
</script>
<style lang="scss" scoped>
	section.rightlist-grszzl{
		margin:20px 0 60px 14px;
		padding:0 27px;
		width:900px;
    min-height: 600px;
    background-color:#fff;
		float: left;
		box-shadow: 0px 2px 3px rgba(0,0,0,0.1);
		opacity: 0.93;
		border-radius: 5px;
		h2{
			font-size: 28px;
			color: #6c6c6c;
			margin:40px auto 30px auto;
		}
		.timesx{
			height:40px;
			border-bottom:1px solid #dcdcdc;
			.active{
				color: #EA6457;
			}
			a{
				font-size: 14px;
				color: #444;
				padding:0 15px 0 0;
				margin-right: 15px;
				border-right: 1px solid #dcdcdc;
				&:last-child{
					border:none;
				}
			}
		}
		
		form{
			ul.personaldata{
				li{
					float:none;
					margin:25px auto;
					.img{
						width:110px;
						height: 110px;
						margin-right:30px; 
						display: inline-block;
						vertical-align: middle;
						overflow: hidden;
						border-radius: 50%;
						img{
							width: 100%;
							height: 100%;
						}
					}
					p{
						width:140px;
						display: inline-block;
					}
					.red{
						&:after{
							content:"*";
							color: red;
						}
					}
					input[type="text"]{
						width: 80%;
						height: 40px;
						outline: none;
						display: inline-block;
						padding-left: 10px;
						background: rgba(249,249,249,1);
						border: 1px solid #d1d1d1;
						border-radius: 5px;
						&::placeholder{font-size: 14px;color: #b3b4b3;}
						&:-ms-input-placeholder{font-size: 14px;color: #b3b4b3;}
						&::-webkit-input-placeholder{font-size: 14px;color: #b3b4b3;}    /* 使用webkit内核的浏览器 */
						&:-moz-placeholder{font-size: 14px;color: #b3b4b3;}                  /* Firefox版本4-18 */
						&::-moz-placeholder{font-size: 14px;color: #b3b4b3;} /* Firefox版本19+ */
					}
					input[type="submit"]{
						width: 120px;
						height: 40px;
						border-radius: 5px;
						&:nth-child(1){
							background-color: #EA6457;
							margin: 0 20px 0 145px ;
							color: #fff;
							border:none;
						}
						&:nth-child(2){
							background-color: #ffffff;
							border:1px solid #949494;
							color:#949494;
						}
					}
					
					textarea{
						width: 80%;
						height: 80px;
						padding-left: 10px;
						resize: none;
						outline: none;
						display: inline-block;
						background: rgba(249,249,249,1);
						border: 1px solid #d1d1d1;
						border-radius: 5px;
						font-size: 14px;
						color: #b3b4b3;
						font-family: "微软雅黑";
						&::placeholder{font-size: 14px;color: #b3b4b3;}
						&:-ms-input-placeholder{font-size: 14px;color: #b3b4b3;}
						&::-webkit-input-placeholder{font-size: 14px;color: #b3b4b3;}    /* 使用webkit内核的浏览器 */
						&:-moz-placeholder{font-size: 14px;color: #b3b4b3;}                  /* Firefox版本4-18 */
						&::-moz-placeholder{font-size: 14px;color: #b3b4b3;} /* Firefox版本19+ */
          }
          .time{
            display: inline-block;
            vertical-align: middle;
            .date{
							width:90px;
							height: 30px;
							display: inline-block;
							vertical-align: middle;
							position: relative;
							select{
								width:90px;
								height: 30px;
								font-size:14px;
								color: #949494;
								position: absolute;
								border:1px solid #d1d1d1;
								outline: none;
								padding:5px;
								-webkit-appearance:none;
								border-radius: 5px;
								option{
									font-size:14px;
									color: #949494;
								}
							}
							.icon{
								width:15px;
								height: 15px;
								right:5px;
								top:5px;
								img{
									width:100%;
									height:100%;
								}
								position: absolute;
							}
          	}
						span{
							font-size: 14px;
							margin: 0 4px;
						}
					}
					.address{
						width: 80%;
						display: inline-block;
						select{
							width:105px;
							height: 30px;
							font-size:14px;
							color: #949494;
							border:1px solid #d1d1d1;
							outline: none;
							border-radius: 5px;
							option{
								font-size:14px;
								color: #949494;
							}
						}
						textarea{
							width:100%;
							margin-top:10px;
						}
					}
					&:nth-child(1){
						a{
							width:120px;
							height: 40px;
							color:#fff;
							display: inline-block;
							line-height: 40px;
							text-align: center;
							border-radius: 5px;
							background-color: #95d3e2;
							box-shadow: 0px 3px 6px rgba(0,77,101,0.16);
						}
					}
					&:nth-child(2){
						input[type="text"]{
							width:220px;
						}
					}
					&:nth-child(4){
						label{
							vertical-align: text-top;
						}
					}
				}
				.tex{
					p{
						vertical-align: bottom;
						height: 90px;
					}
				}
			}
    }
    ul.aqlist{
			margin-top:20px;
			li{
				width:100%;
				padding:20px 0;
				border-bottom:1px solid #f2f2f2;
				h5{
					width:100px;
					height:32px;
					text-align: left;
					padding-left:15px;
					line-height:32px;
					display: inline-block;
					font-size: 14px;
					color:#949494;
				}
				p{
					height: 32px;
					line-height:32px;
					display: inline-block;
					font-size: 14px;
					color: #535353;				
				}
				.btn{
					float:right;
					a{
						width:100px;
						height: 30px;
            display: inline-block;
            vertical-align: bottom;
						text-align: center;
						line-height: 30px;
						border-radius:5px;
					}
					.nonebk{
						border:1px solid #949494;
						height: 28px;
						color:#949494;
						font-size: 14px;
					}
					.havebk{
						background: #EA6457;
						color: #fff;
						font-size: 14px;
					}
				}
			}
		}
	}
</style>



